import { localURL, productionURL } from '../constants.js'
export function ellipsis(str, limit) {
    if (str.length < limit) return str;
    else return str.substr(0, limit) + "â€¦";
}

export async function sleep(time) {
    return new Promise((resolve, reject) => {
        setTimeout(resolve, time);
    });
}

// const DEBUG = false;
const DEBUG = true;
export function cl(s) {
    if (DEBUG === true)
        console.log(s);
}
export function getPageSaveBaseEndPoint() {
    let endpoint = '';
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        endpoint = localURL + '/pagesave';
        // dev code
    } else {
        // production code
        endpoint = productionURL + '/pagesave';
    }
    return endpoint;
}
export function getFriendlyTime(d) {
    cl('before get friendlyTime' + d);
    if (!d) return 'undefined date';
    const date = new Date(d);
    // const month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    // return `${date.getDate()} ${month[date.getMonth()]}, ${date.getHours()}:${date.getMinutes()} `;
    let result = (new Intl.DateTimeFormat('en-US', { dateStyle: 'medium', timeStyle: 'short' }).format(date));
    cl('after get friendlyTime' + result);
    //     before get friendlyTimenull util.js:25
    // after get friendlyTimeDec 31, 1969, 4:00 PM
    // this shows if its null it will return that odd date
    return result;
}
export function getBaseEndPoint() {
    let endpoint = '';
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        endpoint = localURL;
        // dev code
    } else {
        // production code
        endpoint = productionURL;
    }
    return endpoint;
}
export function replaceFirebaseInvalidCharInKey(email) {
    // https://stackoverflow.com/questions/31904123/good-way-to-replace-invalid-characters-in-firebase-keys
    return email.replace(/\./g, ','); // replaces all dots
}