import axios from 'axios';
import { getBaseEndPoint, cl } from '../../helper/util';
export async function submitURL(url) {
    cl('submitting new archive');
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/pagesave',
            {
                url: url,
            },
            {
                withCredentials: true,
            }
        );
        cl(result.status);
        return {
            success: true,
        };
    } catch (e) {
        cl('submitting new archive fail')
        cl(e.response.status);
        if (e.response.status === 403) {

        }
    }
}