import axios from 'axios';
import { getBaseEndPoint, cl } from '../../helper/util';

export async function login(email, pw) {
    cl('axios submitting:' + email + ' ,' + pw);
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/users/login',
            {
                email: email,
                pw: pw,
            },
            {
                withCredentials: true,
            }
        );
        cl('logging in axios');
        cl(result.status);
        if (result.status === 200) {
            return {
                success: true,
                email: email,
                verified: true,
            };
        }

        if (result.status === 202) {
            return {
                success: true,
                email: email,
                verified: false,
            };
        }
    } catch (e) {
        cl('some stuff detected');

        cl(e.response.status);
        if (e.response.status === 403) {
            cl('incorrect credentials');
            return {
                success: false,
                err: 'incorrect credentials',

            };
        } else { // other code means error
            cl('login faileddue to some other');
            return {
                success: false,
                err: 'Try again later',

            };
        }
    }
}

