import { createSlice } from '@reduxjs/toolkit';
import { resendVerifyAsync } from '../login/LoginSlice';

const initialState = {
    isShown: false,
    alertType: 'info',
    msg: 'some s',
};

export const snackbarSlice = createSlice({
    name: 'snackbar',
    initialState,
    reducers: {
        showInfoMsg: (state, action) => {
            state.alertType = 'info';
            state.msg = action.payload;
            state.isShown = true;
        },
        dismissMsg: (state, action) => {
            state.isShown = false;
        },
    },
    extraReducers: (builder) => {
        builder.addCase(resendVerifyAsync.fulfilled, (state, action) => {
            state.isShown = true;
            state.alertType = 'success';
            state.msg = 'Email sent. Please check your email';
        })
            .addCase(resendVerifyAsync.rejected, (state, action) => {
                state.isShown = true;
                state.alertType = 'error';
                state.msg = 'Error resending email, try again a few minutes later...'; //
            })
            ;
    }
});



export const selectSnackbarIsShown = (state) => state.snackbar.isShown;
export const selectSnackbarAlertType = (state) => state.snackbar.alertType;
export const selectSnackbarMsg = (state) => state.snackbar.msg;

export const { showInfoMsg, dismissMsg } = snackbarSlice.actions;
export default snackbarSlice.reducer;