import { Button, Typography } from "@material-ui/core";
import verifyimg from '../resources/Mail-rafiki 1.svg';
import { useSelector, useDispatch } from 'react-redux';
import { selectSignupEmail } from '../features/signup/SignupSlice';
import { resendVerifyAsync, selectResendPending } from '../features/login/LoginSlice';

function VerifyEmail() {
    const signupEmail = useSelector(selectSignupEmail);
    const dispatch = useDispatch();
    let resendPending = useSelector(selectResendPending);

    return (
        <div style={{
            paddingTop: "128px",
            display: "flex",
            flexDirection: 'column',
            alignItems: 'center',
            paddingLeft: "24px",
            paddingRight: "24px",
            paddingBottom: "128px",
            minHeight: "100vh",
        }}>
            <Typography variant="h5" style={{ marginTop: "48px", }} align="center">
                Verify Your Email
            </Typography>
            <Typography variant="caption" align="center" style={{ marginTop: "4px" }}>
                You will need to verify your email to complete registration.
            </Typography>
            <img alt='' src={verifyimg} style={{ width: "256px", marginTop: "8px" }} />
            <Typography variant="caption" align="center" style={{ color: "#828282", maxWidth: "50ch", }} >
                An email has been sent to {signupEmail} with a link to verify your account. If you have not received the email after a few minutes, please check your spam folder
            </Typography>
            <Button
                variant="contained"
                color="primary"
                style={{ marginTop: "16px" }}
                disabled={resendPending}
                onClick={() => { dispatch(resendVerifyAsync(signupEmail)) }}
            >
                {resendPending ? " Resending..." : "Resend email"}
            </Button>
        </div>
    );
}
export default VerifyEmail;