
import Button from '@material-ui/core/Button';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import withWidth from '@material-ui/core/withWidth';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Cookies from 'js-cookie';
import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {
    Link,
    useLocation
} from "react-router-dom";
import logo from '../../resources/Group 2.svg';
import { hide, loadUserFromCookie, logoutAsync } from '../login/LoginSlice';
import { cl } from '../../helper/util';

const useStyles = makeStyles((theme) => ({
    header: {
        paddingLeft: "16px",
        paddingRight: "16px",
        background: "white",
        // position: "fixed",
        zIndex: 1,
        top: "0px",
        display: "flex",
        paddingTop: "16px",
        width: "100%",
        justifyContent: "space-between",
        [theme.breakpoints.up('sm')]: {
            paddingLeft: "32px",
            paddingRight: "32px",
            paddingTop: "16px",
        },
        [theme.breakpoints.up('lg')]: {
            paddingLeft: "128px",
            paddingRight: "128px",
            paddingTop: "32px",
        },
    },
    logo: {
        width: "40px",
        height: "40px",
        [theme.breakpoints.up('lg')]: {
            width: "48px",
            height: "48px",

        },
    },
    makeItSquare: {
        alignSelf: 'center',
    }
}));

function NavBar({ isLoggedOn, userEmail, width, handleClickOpen }) {
    // depending on the login state
    const classes = useStyles();
    const theme = useTheme();
    window.aa = theme; //type aa to see the theme

    let location = useLocation();
    let isAtSignUpPage = location.pathname === "/signup" || location.pathname === '/verify';
    cl('location');
    cl(location);
    let dispatch = useDispatch();
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    useEffect(() => {
        if (Cookies.get('email')) {
            dispatch(loadUserFromCookie({
                email: Cookies.get('email'), // as long asi dont setthis cookie the user will not be logged in
            }));
        }
    }, [dispatch]);

    const handleLogout = async (event) => {
        cl('running logout');
        handleClose(); // close the old logout bubble menu
        handleClickOpen(event); //i think this for log out animatino
        await dispatch(logoutAsync());
        await dispatch(hide());
    }


    return (
        <header className={classes.header} >
            <div style={{
                display: "flex",
                alignItems: "center",
            }}>
                <img alt='logo' src={logo} className={classes.logo} />
                <Link to="/">
                    <h5 style={{ marginLeft: "8px" }}>Page saver</h5>
                </Link>
            </div>

            <Hidden xsDown>
                {!isLoggedOn ? !isAtSignUpPage && (
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <Button onClick={handleClickOpen} >Login</Button>
                        <Button><Link to="/signup">Signup</Link></Button>
                    </div>)
                    : (
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <Typography variant="h5" >
                                {userEmail}
                            </Typography>
                            <Button
                                style={{ marginLeft: "32px" }}
                                onClick={handleLogout}
                            >Log Out</Button>
                        </div>

                    )
                }
            </Hidden>

            <Hidden smUp>
                {isLoggedOn && (
                    <>
                        <IconButton
                            aria-label="expand-more"
                            onClick={handleClick}
                            size='medium'
                            classes={{
                                root: classes.makeItSquare,
                            }}
                        >
                            <ExpandMoreIcon />
                        </IconButton>
                        <Menu
                            id="simple-menu"
                            anchorEl={anchorEl}
                            // keepMounted
                            open={anchorEl !== null}
                            onClose={handleClose}
                        >
                            <MenuItem
                                onClick={handleLogout}
                            >Logout</MenuItem>
                        </Menu>
                    </>
                )}
            </Hidden>
            {/* Current width: {width} */}
        </header>
    )
}

export default withWidth()(NavBar);