(async function () {
  try {
    // this will say it is insecure
    // window.registration = await navigator.serviceWorker.register('./pagesavepublicsite/firebase-messaging-sw.js');
    // so i had to use this
    // window.registration = await navigator.serviceWorker.register('./firebase-messaging-sw.js');
    // have to write full path it seems
    //TODO: have to edit this later for production site

    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
      cl('serviceworker installing dev')
      // window.registration = await navigator.serviceWorker.register('https://localhost:3000/pagesavepublicsite/firebase-messaging-sw.js');
      const swUrl = `${process.env.PUBLIC_URL}/firebase-messaging-sw.js`;
      window.registration = await navigator.serviceWorker.register(swUrl);
      // dev code
      cl('success serviceworker');
    } else {
      // production code
      cl('serviceworker installing production version')
      // window.registration = await navigator.serviceWorker.register('https://mightbehuman-creator.github.io/pagesavepublicsite/firebase-messaging-sw.js');
      const swUrl = `${process.env.PUBLIC_URL}/firebase-messaging-sw.js`;
      // window.registration = await navigator.serviceWorker.register('./pagesavepublicsite/firebase-messaging-sw.js');
      window.registration = await navigator.serviceWorker.register(swUrl);
      cl('success serviceworker');
    }
    cl('ServiceWorker registration successful with scope: ', window.registration.scope);
  } catch (err) {
    cl('ServiceWorker registration failed: ', err);
  }
}());

