import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { useDispatch, useSelector } from 'react-redux';
import { dismissMsg, selectSnackbarAlertType, selectSnackbarIsShown, selectSnackbarMsg } from './GlobalSnackbarSlice';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function GlobalSnackbar() {

    const alertType = useSelector(selectSnackbarAlertType);
    const isShown = useSelector(selectSnackbarIsShown);
    const msg = useSelector(selectSnackbarMsg);
    const dispatch = useDispatch();

    const handleClose = (event, reason) => {
        // if (reason === 'clickaway') {
        //     return;
        // }

        dispatch(dismissMsg());
    };
    return (
        <Snackbar open={isShown} autoHideDuration={6000} onClose={handleClose}>
            {/* // <Snackbar open={isShown} > */}
            <Alert severity={alertType}>{msg}</Alert>
        </Snackbar>
    );
}