// A wrapper for <Route> that redirects to the login
// screen if you're not yet authenticated.
function PrivateRoute({ children, ...rest }) {
    // let auth = useAuth();
    // let isLoggedIn = useSelector(selectIsLoggedIn);
    //this approach not okay because the state is removed on refresh. 
    //Important to keep this in mind when choosing whether to keep in state or localstorage and cookie
    // should read from cookie instead
    let isLoggedIn = !!(Cookies.get('email'));
    return (
        <Route
            {...rest}
            render={({ location }) =>
                isLoggedIn ? (
                    children
                ) : (
                    <Redirect
                        to={{
                            // pathname: "/login",
                            // pathname: "/pagesavepublicsite/signup",
                            pathname: "/",
                            state: { from: location }
                        }}
                    />
                )
            }
        />
    );
}


function SimpleDialog(props) {
    // const classes = useStyles();
    const { open } = props;


    return (
        <Dialog aria-labelledby="Server currently offline" p={5} open={open}>
            <Box p={6}>
                <DialogTitle id="simple-dialog-title">
                    <Typography variant='h3' align='center'>Server is currently offline</Typography></DialogTitle>
                <Typography variant='body1' align='center'>Please wait while we boot up the server...</Typography>
                <div style={{ marginTop: '24px' }}>
                    <Lottie animationData={bootingAnimation} />;
                </div>
            </Box>
        </Dialog>
    );
}

function App() {
    let dispatch = useDispatch();
    let isLoginShown = useSelector(selectIsLoginShown);
    // let isLogoutShown = true;

    const theme = createMuiTheme({
        palette: {
            primary: {
                main: "#333333",
            }
        },
        typography: {
            // fontFamily: 'Raleway, Arial',
            fontFamily: ['Lato', 'sans-serif'],
            h1: {
                fontSize: "48.83px",
                lineHeight: "53.96px",
                fontWeight: "400",
            },

            h2: {
                fontSize: "39.06px",
                lineHeight: "38.67px",
                fontWeight: "400",
            },

            h3: {
                fontSize: "31.25px",
                lineHeight: "34.53px",
                fontWeight: "400",
            },

            h4: {
                fontSize: "25px",
                lineHeight: "31.37px",
                fontWeight: "400",
            },

            h5: {
                fontSize: "20px",
                lineHeight: "32.9px",
                fontWeight: "400",
            },

            body1: {
                fontSize: "16px",
                lineHeight: "23.2px",
                fontWeight: "400",
            },

            caption: {
                fontSize: "12.8px",
                lineHeight: "17.54px",
                fontWeight: "400",
            },

            subtitle1: { // my equivalent of minicaption in my design
                fontSize: "10.24px",
                lineHeight: "15.82px",
                fontWeight: "400",
            },
            // overrides: {
            //     MuiCssBaseline: {
            //         '@global': {
            //             '@font-face': [raleway],
            //         },
            //     },
            // },
        },
    });
    // let baseliner = new Baseliner(8);
    //user selector to fetch login status
    const isLoggedIn = useSelector(selectIsLoggedIn);
    const username = useSelector(selectUsername);
    // 
    const [status, setStatus] = useState("online");

    useEffect(() => {
        var ref = db.ref("status");
        ref.on('value', (snapshot) => {
            cl('status: ' + snapshot.val());
            setStatus(snapshot.val());
        }, (errorObject) => {
            cl('The read failed: ' + errorObject.name);
        });
    }, []);



    const handleClickOpen = () => {
        dispatch(show());
    };

    const handleClose = (value) => {
        dispatch(hide());
    };

    return (
        <ThemeProvider theme={theme}>
            {/* <Router basename="/pagesavepublicsite"> */}
            <Router basename="/">
                <CssBaseline />
                {/* <ColumnGrid offset="320px" columns={12} gutter="40px" /> */}
                {/* <GridBoxes size={8} /> */}
                <div style={{
                    position: "relative",
                    minHeight: '100vh',
                }}>
                    <NavBar handleClickOpen={handleClickOpen} isLoggedOn={isLoggedIn} userEmail={username} />
                    <Login open={isLoginShown} onClose={handleClose} />
                    <Switch>
                        <Route path="/signup">
                            <Signup />
                        </Route>
                        <Route path="/verify">
                            <VerifyEmail />
                        </Route>
                        <Route path="/faq">
                            <FAQ />
                        </Route>
                        <Route exact path="/">
                            <HomePage isLoggedOn={isLoggedIn} handleClickOpen={handleClickOpen} />
                        </Route>
                        <PrivateRoute exact path="/dashboard">
                            <Dashboard />
                        </PrivateRoute>
                        <Route path="*">
                            <NoMatch />
                        </Route>
                        {/* for future jump to specific pagesave https://reactrouter.com/web/guides/quick-start/2nd-example-nested-routing */}
                    </Switch>
                    <Footer />
                </div>
            </Router>
            <ProgressDialog />
            <SimpleDialog open={status === 'offline'} />
            <GlobalSnackbar />
        </ThemeProvider >
    );
}
