import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import React from 'react';
import { useDispatch } from 'react-redux';
import validUrl from 'valid-url';
// import styles from './SubmissionBar.module.css';
import { showInvalidURLMsg, submitURLAsync } from './submissionbarSlice';
import { cl } from '../../helper/util';

const useStyles = makeStyles((theme) => ({
    container: {
        marginTop: theme.spacing(2),
        // height: "500px",
    },
    borderRadiusOverwrite: {
        borderRadius: '10px',
    },
    savebutton: {
        // minWidth: "150px",
        marginTop: '16px',
        padding: '1em 2em',
    },
    input: {
        width: "100%",
    },
    root: {

    },
}));


export default function SubmissionBar() {
    // const [submissiontext, setSubmissionText] = useState('');
    // we use uncontrolled ref
    // need media query later for the submission button below if size is too small
    const dispatch = useDispatch();;

    let textRef = React.createRef();
    let submitURL = (e) => {
        e.preventDefault();
        let url = textRef.current.value;
        cl(url);
        if (validUrl.isUri(url)) {
            cl('Looks like an URI');
            dispatch(submitURLAsync(url));
            textRef.current.value = '';
        } else {
            cl('Not a URI');
            dispatch(showInvalidURLMsg());
        }

    };

    const classes = useStyles();
    return (
        <div className={classes.container}>
            <form
                // className={classes.root}
                onSubmit={submitURL}
                noValidate
                autoComplete="off"
            // style={{ height: "600px" }}
            >
                <TextField
                    inputRef={textRef}
                    className={classes.input}
                    variant="outlined"
                    label="Enter url of page to save"
                    classes={{
                        root: classes.borderRadiusOverwrite,
                    }}
                    InputProps={{
                        classes: {
                            root: classes.borderRadiusOverwrite,
                        }
                    }}
                />
                <Button
                    className={classes.savebutton}
                    // startIcon={<SaveIcon />}
                    variant="contained"
                    color="primary"
                    onClick={submitURL}
                >Save</Button>
            </form>
        </div>
    )
}