
import { Box, Button, Dialog, IconButton, InputAdornment, makeStyles, Slide, Typography } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import { useFormik } from 'formik';
import Lottie from 'lottie-react';
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect } from 'react-router-dom';
import * as yup from 'yup';
import sadface from '../../resources/iconoir_emoji-sad.svg';
import loadingAnimation from "../../resources/lf30_editor_uekpjvgl.json";
import { showInfoMsg } from '../globalsnackbar/GlobalSnackbarSlice';
import { forgetpw, loginAsync, relogin, resendVerifyAsync, selectJustLoggedIn, selectLoginScreen, selectResendPending } from './LoginSlice';

const useStyles = makeStyles((theme) => ({
    changeLoginDialogAlign: {
        // justifyContent: 'flex-end',
        // alignItems: 'flex-start',
        justifyContent: 'center',
        alignItems: 'center',

        // borderRadius: '40px',
        '& .MuiPaper-rounded': {
            borderRadius: '20px',
        },

        [theme.breakpoints.up('md')]: {
            justifyContent: 'flex-end',
            alignItems: 'flex-start',

        }
    },
    inputInternalFieldSetHover: {
        '&:hover .MuiOutlinedInput-notchedOutline': {
            borderColor: 'white',

        },
        '&.MuiOutlinedInput-notchedOutline': {
            borderColor: 'white',

        },
        '&.Mui-error .MuiOutlinedInput-notchedOutline': {
            borderColor: 'orange',
        }
    },
    fieldsetBorder: {
        border: 'solid 1px white',
    },
    roundedBorder: {
        borderRadius: '50px',

    },
    forgetpasswordbtn: {
        color: '#fff',
        marginTop: '16px',
        fontSize: theme.typography.subtitle1.fontSize,
    },
    white: {
        color: '#fff',
    },
    logoutwrapper: {
        padding: '48px',
        background: '#4B6A5E',
        margin: 'auto',
    },

}));
const validationSchema = yup.object({
    email: yup
        .string('Enter your email')
        .email('Enter a valid email')
        .required('Email is required'),
    password: yup
        .string('Enter your password')
        .min(8, 'Password should be of minimum 8 characters length')
        .required('Password is required'),
});
const forgetPWValidationSchema = yup.object({
    email: yup
        .string('Enter your email')
        .email('Enter a valid email')
        .required('Email is required'),
});
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
});

function Login(props) {
    const classes = useStyles();
    const { onClose, selectedValue, open } = props;
    // let [loginScreen, setLoginScreen] = useState('home');
    // home, forget, invalid, logging in, 
    let loginScreen = useSelector(selectLoginScreen);
    let isJustLoggedIn = useSelector(selectJustLoggedIn);
    let resendPending = useSelector(selectResendPending);

    let dispatch = useDispatch();

    const handleClose = () => {
        // setLoginScreen('home'); // reset to login screen when closing dialog
        dispatch(relogin());
        onClose(selectedValue);
    };


    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
            // email: 'malay@gmail.com',
            // password: '11111111',
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            // alert(JSON.stringify(values, null, 2));
            //dispatch some action
            dispatch(loginAsync({ email: values.email, pw: values.password }));
        },
    });

    const forgetPWFormik = useFormik({
        initialValues: {
            email: 'foobar@example.com',
        },
        validationSchema: forgetPWValidationSchema,
        onSubmit: (values) => {
            dispatch(showInfoMsg('Not implemented yet'))
        },
    });


    // let isAtLogin = loginScreen === 'home';
    return (
        <Dialog
            TransitionComponent={Transition}
            onClose={handleClose}
            aria-labelledby="simple-dialog-title"
            classes={{
                scrollPaper: classes.changeLoginDialogAlign,
                root: classes.roundedBorder
            }}
            open={open}
        // open={true}
        >
            {/* start of login first screen */}
            {loginScreen === 'home' &&
                < div
                    style={{
                        background: "#4B6A5E",
                        display: "flex",
                        flexDirection: 'column',
                        padding: '48px 24px',
                    }}
                >
                    <Typography variant='h4' align='center' style={{ color: '#fff' }}>Welcome</Typography>
                    <Typography variant='h4' align='center' style={{ color: '#fff' }} >to</Typography>
                    <Typography variant='h4' align='center' style={{ color: '#fff' }} >Pagesaver</Typography>

                    <form onSubmit={formik.handleSubmit} style={{ display: 'flex', marginTop: '24px', flexDirection: 'column', }}>
                        <TextField
                            // fullWidth
                            id="email"
                            name="email"
                            label="Email"
                            value={formik.values.email}
                            onChange={formik.handleChange}
                            error={formik.touched.email && Boolean(formik.errors.email)}
                            helperText={formik.touched.email && formik.errors.email}
                            variant='outlined'
                            FormHelperTextProps={{
                                style: { color: 'orange', }
                            }}
                            InputLabelProps={{
                                style: { color: '#fff', }
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <MailOutlineIcon />
                                    </InputAdornment>
                                ),
                                style: {
                                    color: '#fff',
                                },
                                classes: {
                                    root: classes.inputInternalFieldSetHover,
                                    notchedOutline: classes.fieldsetBorder,
                                }
                            }}
                        />
                        <TextField
                            // fullWidth
                            id="password"
                            name="password"
                            label="Password"
                            type="password"
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            error={formik.touched.password && Boolean(formik.errors.password)}
                            helperText={formik.touched.password && formik.errors.password}
                            // color='#ffffff'
                            style={{ marginTop: "16px" }}
                            variant='outlined'
                            InputLabelProps={{
                                style: { color: '#fff', }
                            }}
                            FormHelperTextProps={{
                                style: { color: 'orange', }
                            }}
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <VpnKeyIcon />
                                    </InputAdornment>
                                ),
                                style: {
                                    color: '#fff',
                                },
                                classes: {
                                    root: classes.inputInternalFieldSetHover,
                                    notchedOutline: classes.fieldsetBorder
                                }
                            }}
                        />

                        <Button type="submit" variant='outlined' style={{ marginTop: "24px", color: '#fff', borderRadius: '60px', border: '1px solid #fff' }} >Sign in</Button>
                    </form>

                    <Button
                        className={classes.forgetpasswordbtn}
                        onClick={() => dispatch(forgetpw())}
                    >Forget password</Button>
                </div>
            }
            {/* end of login first screen */}
            {loginScreen === 'verifyemailpls' &&
                (< div
                    style={{
                        background: "#4B6A5E",
                        display: "flex",
                        flexDirection: 'column',
                        padding: '48px 24px',
                    }}
                >
                    <Typography variant='h4' align='center' style={{ color: '#fff' }}>Verify your email</Typography>
                    <Typography variant='body1' align='center' style={{ color: '#fff' }}>
                        Please check your email for the verification first.
                    </Typography>

                    <Button
                        variant='outlined'
                        style={{ marginTop: "48px", color: '#fff', borderRadius: '60px', border: '1px solid #fff' }}
                        onClick={
                            () => {
                                dispatch(relogin())
                            }
                        }
                    >
                        ok
                    </Button>
                    <Button
                        style={{ marginTop: "8px", color: '#fff' }}
                        disabled={resendPending}
                        onClick={() => dispatch(resendVerifyAsync(formik.values.email))}
                    >
                        {resendPending ? " Resending..." : "Resend email"}

                    </Button>
                </div>)}
            {loginScreen === 'forget' &&
                (<div style={{ position: 'relative' }}>
                    <div
                        style={{
                            padding: '24px',
                            background: '#4B6A5E',
                            paddingBottom: '128px',
                            maxWidth: '350px',
                        }}
                        className={classes.tophalf}
                    >
                        <IconButton
                            color="secondary"
                            aria-label="back to login"
                            classes={{
                                root: classes.white,
                            }}
                            onClick={() => dispatch(relogin())}
                        >
                            <ArrowBackIcon />
                        </IconButton>
                        <Typography
                            align='center'
                            variant='h4'
                            style={{
                                color: '#fff'
                            }}
                        >Forget Your Password?</Typography>
                        <Typography
                            align='center'
                            variant='h5'
                            style={{
                                color: '#fff'
                            }}
                        >Enter your email to reset your password</Typography>
                    </div>
                    <div
                        style={{
                            padding: '24px',
                            background: '#fff',
                            paddingTop: '128px',
                            paddingBottom: '24px',

                        }}
                        className={classes.bottomhalf}
                    >
                        <Typography
                            align='center'
                        >Didnâ€™t receive a mail?</Typography>
                        <Typography
                            align='center'
                        >Resend email</Typography>
                    </div>
                    <div
                        style={{
                            width: '100%',
                            height: '100%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            position: 'absolute',
                            top: '0px',
                            left: '0px',
                            pointerEvents: 'none',
                        }}

                    >
                        <Box
                            boxShadow={3}
                            bgcolor="background.paper"
                            m={1}
                            p={3}
                            style={{
                                width: '80%',
                                borderRadius: '20px',
                            }}>
                            <form onSubmit={forgetPWFormik.handleSubmit}>
                                <TextField
                                    fullWidth
                                    id="email"
                                    name="email"
                                    label="Email"
                                    value={forgetPWFormik.values.email}
                                    onChange={forgetPWFormik.handleChange}
                                    inputProps={{
                                        style: {
                                            pointerEvents: 'initial',
                                        }
                                    }}
                                    error={forgetPWFormik.touched.email && Boolean(forgetPWFormik.errors.email)}
                                    helperText={forgetPWFormik.touched.email && forgetPWFormik.errors.email}
                                />
                                <Button
                                    color="primary"
                                    variant="contained"
                                    style={{ marginTop: '16px', pointerEvents: 'initial', }}
                                    fullWidth
                                    onClick={() => dispatch(showInfoMsg('Not implemented yet'))}
                                    type="submit">
                                    reset
                                </Button>
                            </form>
                        </Box>
                    </div>
                </div >)}
            {/* end of forget screen */}
            {loginScreen === 'logging in' && (
                <div style={{
                    padding: '48px',
                    background: '#4B6A5E',
                }}>
                    <Typography
                        variant='h4'
                        style={{
                            color: '#fff'
                        }}
                    >Logging in â€¦</Typography>
                    <div style={{ marginTop: '24px' }}>
                        <Lottie animationData={loadingAnimation} />;
                    </div>
                </div>
            )}
            {loginScreen === 'logging out' && (
                <div className={classes.logoutwrapper} >
                    <Typography
                        variant='h4'
                        style={{
                            color: '#fff'
                        }}
                    >Logging out â€¦</Typography>
                    <div style={{ marginTop: '24px' }}>
                        <Lottie animationData={loadingAnimation} />;
                    </div>
                </div>
            )}
            {loginScreen === 'invalid' && (
                <div style={{
                    padding: '48px',
                    background: '#4B6A5E',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}>
                    <Typography
                        variant='h4'
                        align='center'
                        style={{
                            color: '#fff'
                        }}
                    >Invalid Credentials</Typography>
                    <Typography
                        variant='h5'
                        align='center'
                        style={{
                            color: '#fff',
                            maxWidth: '20ch',
                            marginTop: '16px',
                        }}
                    >Please check your credentials and try again</Typography>
                    <img alt='' src={sadface} />
                    <Button
                        variant='outlined'
                        style={{
                            borderColor: 'rgba(255,255,255,0.2)',
                            color: '#fff',
                            borderRadius: '20px',
                            marginTop: '16px',
                        }}
                        onClick={
                            () => dispatch(relogin())
                        }
                    >Try again</Button>
                </div>
            )}
            {isJustLoggedIn ?
                <Redirect
                    to={{
                        pathname: "/dashboard",
                        // state: { from: location }
                    }}
                /> : null}
        </Dialog >
    );
}
export default Login;