import { Button, Hidden, TextField, Typography } from "@material-ui/core";
import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import MuiAlert from '@material-ui/lab/Alert';
import 'firebase/auth';
import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import {
    // BrowserRouter as Router,
    // Switch,
    // Route,
    // Link,
    Redirect
} from "react-router-dom";
import * as yup from 'yup';
import signuppic from '../../resources/undraw_quick_chat_re_bit5 1.svg';
import { resetExistingAlert, selectSignupStatus, signupAsync } from "./SignupSlice";
import { cl } from '../../helper/util';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    page: {
        paddingTop: "128px",
        minHeight: "100vh",

    },
    entry: {
        display: "flex",
        flexDirection: "column",
        paddingLeft: "24px",
        paddingRight: "24px",
        maxWidth: "512px",
        [theme.breakpoints.up('xs')]: {
            flex: '1 1 50%',
        },
    },
    entrywrapper: {
        marginTop: "64px",
        [theme.breakpoints.up('xs')]: {
            display: "flex",
            flexDirection: "row",
            justifyContent: 'center',
            alignItems: 'center',
        },
    },
    inputBox: {
        borderRadius: "10px",
        padding: "2em 1em",
    },
    signuppicWrapper: {
        display: "flex",
        justifyContent: "center",
        flex: '1 1 20%',
        [theme.breakpoints.up('sm')]: {
            flex: '0 1 50%',
        },
        [theme.breakpoints.up('md')]: {
            flex: '0 1 auto',
        },
    },
    signuppic: {
        // marginLeft: "40px",
        width: "min(512px,90%)",
    },
    btn: {
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: 'initial',
        }
    }

}));

const validationSchema = yup.object({
    email: yup
        .string('Enter your email')
        .email('Enter a valid email')
        .required('Email is required'),
    password: yup
        .string('Enter your password')
        .min(8, 'Password should be of minimum 8 characters length')
        .required('Password is required'),
    confirmpassword: yup
        .string('Reenter your password')
        .required('Reenter your password')
        .oneOf([yup.ref('password')], 'Password did not match'),
});



function Signup() {
    const classes = useStyles();
    const dispatch = useDispatch();

    // i think something to do with props dependence maybe use useeffect?
    // i need to pass it in or use effect or something
    // i think i am using redux the wrong way since the state will not change, if i use selector
    let openAlreadySignup = useSelector(selectSignupStatus) === 'existing';
    let signupPending = useSelector(selectSignupStatus) === 'pending';
    let signupError = useSelector(selectSignupStatus) === 'error';
    let signupDone = useSelector(selectSignupStatus) === 'done';

    // let history = useHistory();
    // let location = useLocation();
    //or do i use the redirect compoonent from router
    // https://reactrouter.com/web/api/Redirect


    const handleClose = () => {
        dispatch(resetExistingAlert());
        cl('handle close is called');
    }

    const formik = useFormik({
        initialValues: {
            email: '',
            password: '',
            confirmpassword: '',
            // email: 'sonohex@gmail.com',
            // password: 'foobarbar',
            // confirmpassword: 'foobarbar',
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            // my server
            dispatch(signupAsync(
                { email: values.email, pw: values.password }
            ));

            //using firebase
            // firebaseSignup(values.email, values.password);
        },
    });


    return (
        <div
            className={classes.page}
        >
            <div className={classes.entrywrapper}>

                <div className={classes.entry}>
                    <Typography variant="h5">
                        Sign up for page saver.<br />
                        Itâ€™s free
                    </Typography>
                    <form onSubmit={(e) => { e.preventDefault(); formik.handleSubmit(e) }} style={{ marginTop: "24px" }}>
                        <TextField
                            fullWidth
                            id="email"
                            name="email"
                            label="Email"
                            value={formik.values.email}
                            disabled={signupPending}
                            onChange={formik.handleChange}
                            error={formik.touched.email && Boolean(formik.errors.email)}
                            helperText={formik.touched.email && formik.errors.email}
                        />
                        <TextField
                            fullWidth
                            id="password"
                            name="password"
                            label="Password"
                            type="password"
                            disabled={signupPending}
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            error={formik.touched.password && Boolean(formik.errors.password)}
                            helperText={formik.touched.password && formik.errors.password}
                        />
                        <TextField
                            fullWidth
                            id="confirmpassword"
                            name="confirmpassword"
                            label="Confirm Password"
                            type="password"
                            disabled={signupPending}
                            value={formik.values.confirmpassword}
                            onChange={formik.handleChange}
                            error={formik.touched.confirmpassword && Boolean(formik.errors.confirmpassword)}
                            helperText={formik.touched.confirmpassword && formik.errors.confirmpassword}
                        />
                        <Button disabled={signupPending} color="primary" className={classes.btn} style={{ marginTop: "24px" }} variant="contained" type="submit">
                            {signupPending ? "Please Wait..." : "Sign up"}
                        </Button>
                    </form>

                </div>
                <Hidden xsDown>
                    <div className={classes.signuppicWrapper}>
                        <img alt='' src={signuppic} className={classes.signuppic}></img>
                    </div>
                </Hidden>
            </div>

            <Snackbar open={openAlreadySignup} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="error">
                    A user with this email has already signed up.
                </Alert>
            </Snackbar>
            <Snackbar open={signupError} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity="warning">
                    Please try again later. Server might be experiencing high load.
                </Alert>
            </Snackbar>
            {signupDone ? <Redirect
                to={{
                    pathname: "/verify",
                    // state: { from: location }
                }}
            /> : null}
        </div>
    );
}


export default Signup;