import { Typography } from "@material-ui/core";
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link'; // i think maybe use the link from router instead
import Snackbar from '@material-ui/core/Snackbar';
import { makeStyles } from '@material-ui/core/styles';
import MuiAlert from '@material-ui/lab/Alert';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link as RouterLink } from "react-router-dom";
import Archive from "../archive/Archive";
import { resetJustLoggedIn, selectJustLoggedIn } from '../login/LoginSlice';
import SubmissionBar from "../submissionbar/SubmissionBar";
import { closeSB, selectSBMsg, selectSBOpen, selectSBType } from '../submissionbar/submissionbarSlice';
import { cl } from '../../helper/util';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    dashboardPadding: {
        marginLeft: "auto",
        marginRight: "auto",
        padding: "24px",
        maxWidth: "1280px",
        paddingBottom: "128px",
        [theme.breakpoints.up('sm')]: {
            padding: '48px',
            paddingBottom: '128px',
        }
    },
    topSection: {
        marginLeft: "auto",
        marginRight: "auto",
        maxWidth: '340px',
        [theme.breakpoints.up('sm')]: {
            marginLeft: '0px',
        }

    },
}));
export default function Dashboard() {
    function handleClick(event) {
        event.preventDefault();
        console.info('You clicked a breadcrumb.');
    }
    const isSBOpen = useSelector(selectSBOpen);
    const SBMsg = useSelector(selectSBMsg);
    const SBMsgType = useSelector(selectSBType);
    const dispatch = useDispatch();

    let isJustLoggedIn = useSelector(selectJustLoggedIn);
    useEffect(() => {
        dispatch(resetJustLoggedIn());
        cl('dashboard login resetting');
    }, [isJustLoggedIn, dispatch]);
    useEffect(() => {
        cl('dashboard login mounted');
    }, []);

    const classes = useStyles();

    const handleClose = (event, reason) => {

        if (reason === 'clickaway') {
            return;
        }
        cl('trying to close');
        // setOpen(false);
        // cl('type of dispatch: ' + typeof dispatch);
        // cl('type of closeSB: ' + typeof closeSB);
        // cl('closeSB: ' + closeSB);
        dispatch(closeSB());
    };

    return (
        <div className={classes.dashboardPadding} >
            <Breadcrumbs aria-label="breadcrumb">
                <Link color="inherit" href="/" onClick={handleClick}>
                    <RouterLink to="/">
                        Home
                    </RouterLink>
                </Link>
                <Link
                    color="inherit"
                    href="/getting-started/installation/"
                    aria-current="page"
                    onClick={handleClick}>
                    Dashboard
                </Link>
            </Breadcrumbs>
            <div className={classes.topSection} >
                <Typography
                    variant="body1"
                    style={{
                        marginTop: '32px',
                    }}
                >You can have your own personal archive of websites by using the form below.
                </Typography>
                <Typography
                    style={{
                        marginTop: '48px',
                    }}
                    variant="body1"
                >Enter the url of any website and we will save a copy of it for you.
                </Typography>
                <SubmissionBar />
            </div>
            <Archive />
            <Snackbar open={isSBOpen} autoHideDuration={1200} onClose={handleClose}>
                <Alert onClose={handleClose} severity={SBMsgType}>
                    {SBMsg}
                </Alert>
            </Snackbar>
            {/* <ProgressDialog /> */}

        </div>
    );
}