
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import Cookies from 'js-cookie';
import { sleep } from '../../helper/util';
import { signupAsync } from '../signup/SignupSlice';
import { login, logout, resendVerifyEmail } from './LoginAPI';
import { cl } from '../../helper/util';


const initialState = {
    isShown: false,
    isLoggedIn: false,
    username: '',
    screen: 'home',
    // screen: 'verifyemailpls',
    justLoggedIn: false,
    resendPending: false,
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const loginAsync = createAsyncThunk(
    'login/login',
    async ({ email, pw }, { rejectWithValue }) => {
        await sleep(500); // add for animatin purposes
        const resp = await login(email, pw);
        // The value we return becomes the `fulfilled` action payload
        cl(resp);
        if (!resp.success) {
            cl('reject');
            return rejectWithValue(resp.err);
        }

        cl('success logging');
        return resp;
    }
);


export const logoutAsync = createAsyncThunk(
    'login/logout',
    async (email, pw) => {
        await sleep(500); // add for animation purposes
        const response = await logout(email, pw);
        // The value we return becomes the `fulfilled` action payload
        return response;
    }
);

export const resendVerifyAsync = createAsyncThunk(
    'login/resendVerify',
    async (email, pw) => {
        const response = await resendVerifyEmail(email, pw);
        // The value we return becomes the `fulfilled` action payload
        return response;
    }
);
