export const loginSlice = createSlice({
    name: 'login',
    initialState,
    reducers: {
        show: (state, action) => {
            state.isShown = true;
        },
        hide: (state, action) => {
            state.isShown = false;
        },
        // logout: (state) => {
        //     state.isLoggedIn = false;
        //     state.screen = 'home';
        //     // maybe need to change this to be async since we need to remove cookies
        //     //add the same animation as logging in
        // },
        relogin: (state, action) => {
            state.screen = 'home';
        },
        forgetpw: (state) => {
            state.screen = 'forget';
        },
        resetJustLoggedIn: (state, action) => {
            state.justLoggedIn = false;
        },
        loadUserFromCookie: (state, action) => {
            let email = action.payload.email; // there is login.username and signup.email state they are different
            if (typeof email !== 'undefined') {
                state.isLoggedIn = true;
                state.username = email;
            }

        },
    },
    // The `extraReducers` field lets the slice handle actions defined elsewhere,
    // including actions generated by createAsyncThunk or in other slices.
    extraReducers: (builder) => {
        builder
            .addCase(loginAsync.pending, (state, action) => {
                state.screen = 'logging in';
            })
            .addCase(loginAsync.fulfilled, (state, action) => {
                let payload = action.payload;
                if (!payload.verified) {
                    state.screen = 'verifyemailpls';
                } else {
                    state.screen = 'home';
                    state.isLoggedIn += payload.success;
                    state.username = payload.email;
                    // this for temporrary login
                    // record in cookie for persistent over session and reopening, and refreshes
                    Cookies.set('email', action.payload.email, { expires: 7000 });
                    // Cookies.set('test', action.payload.email, { expires: 7000 });
                    // document.cookie = "name=oeschger; jack=hello; ";
                    //it is set for port 5000 for some reason
                    // this cookie is set for :5000 but why
                    // think maybe need dispatch another action thats why i need redux saga?
                    //dispatch another action to close the login bar
                    state.isShown = false;
                    //TODO: maybe save credential into cookie or something so that it can be easily filled?
                    //redirect to dashboard?
                    state.justLoggedIn = true;

                }
            })
            .addCase(loginAsync.rejected, (state, action) => {
                state.screen = 'invalid'; //
                // let payload = action.payload;
                cl('login fail due to');
                cl(action.payload);
                if (action.payload && (action.payload.err === 'incorrect credentials')) {

                } else {
                    // server error
                    //TODO:
                }

            })
            .addCase(logoutAsync.pending, (state, action) => {
                state.screen = 'logging out';
            })
            .addCase(logoutAsync.fulfilled, (state, action) => {
                state.screen = 'home';
                state.isLoggedIn = false;
                state.username = '';
                //TODO: redirect to home page? but rendering a Redirect component seems weird
                // i think have to reset the state too
                // figure out maybe move the redirect to top level or something
                //TODO: i think may have to manually clear the session cookie since the destroy on the server side does not do it
                //clear the cookie
                Cookies.remove('email');
                Cookies.remove('heav');// sesion id 
            })
            .addCase(logoutAsync.rejected, (state, action) => {
                // need indicate some error
                // state.screen = 'home';
                // let payload = action.payload;
                // figure out this part

            })
            .addCase(signupAsync.fulfilled, (state, action) => {
                // state.isLoggedIn = true;
                // state.username = action.payload.email;
            })
            .addCase(resendVerifyAsync.pending, (state, action) => {
                state.resendPending = true;
            })
            .addCase(resendVerifyAsync.rejected, (state, action) => {
                state.resendPending = false;
            })
            .addCase(resendVerifyAsync.fulfilled, (state, action) => {
                //display a toast?
                //might needto add a timeout, to prevent spam
                state.resendPending = false;
            });
        // think need add rejected
    },
});

export const selectLoginScreen = (state) => state.login.screen;
export const selectIsLoginShown = (state) => state.login.isShown;
export const selectIsLoggedIn = (state) => state.login.isLoggedIn;
export const selectUsername = (state) => state.login.username;
export const selectJustLoggedIn = (state) => state.login.justLoggedIn;
export const selectResendPending = (state) => state.login.resendPending;

export const { resetJustLoggedIn, loadUserFromCookie, relogin, forgetpw, show, hide } = loginSlice.actions;

export default loginSlice.reducer;
