import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchArchives as fetchArchivesAPI, registerMessageToken, turnOffAlert, turnOnAlert } from './archiveAPI';
import { cl } from '../../helper/util';

const initialState = {
    archives: [],
    status: 'loading',
    pending: {

    },
};


export const registerMessageTokenAsync = createAsyncThunk(
    'archive/registerMessageToken',
    async (token) => {
        const response = await registerMessageToken(token);
        return response;
    }
);

export const fetchArchivesAsync = createAsyncThunk(
    'archive/fetchArchives',
    async () => {
        const response = await fetchArchivesAPI();
        return response.data;
    }
);

export const turnOnAlertAsync = createAsyncThunk(
    'archive/turnOnAlert',
    async (id, meta) => {
        cl(`id received: ${id}`)
        cl(`request id ${meta.requestId}`);
        // meta.dispatch() and meta.getState() , we can do something
        cl(meta);
        meta.dispatch(setPending(id));
        const response = await turnOnAlert(id);
        cl('response here:');
        cl(response);
        return response;
    }
);
export const turnOffAlertAsync = createAsyncThunk(
    'archive/turnOffAlert',
    async (id, meta) => {
        cl(`id received: ${id}`)
        meta.dispatch(setPending(id));
        const response = await turnOffAlert(id);
        return response;
    }
);
export const archiveSlice = createSlice({
    name: 'archive',
    initialState,
    reducers: {
        setPending: (state, action) => {
            cl(action.payload);
            return {
                ...state,
                pending: {
                    ...state.pending,
                    [action.payload]: true,
                },
            }

        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchArchivesAsync.pending, (state) => {
                // state.status = 'loading';
            })
            .addCase(fetchArchivesAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                state.archives = action.payload || [];
            })
            .addCase(turnOnAlertAsync.pending, (state, action) => {
                cl('turning on alertpending');
            })
            .addCase(turnOnAlertAsync.fulfilled, (state, action) => {
                // TODO: maybe i should receive the next date to display instead of calculating locally
                cl('turning on alert complete');
                cl(action);
                cl(`payload: ${action.payload}`);
                // in success case  pyaload contains 
                // {
                //     success:true,
                //     alertsetat: somenumber,
                //     id: id
                // }
                const { id, alertsetat, title } = action.payload;
                cl(`alertsetat: ${alertsetat}`);
                cl(`id to remove from pending from turning on: ${id}`);
                let newAlertString = (new Date(Number(alertsetat))).toString();
                cl('new alert string');
                cl(newAlertString);
                new Notification('The next notification will be 1 day later', {
                    body: `Reminder for ${title}`,
                    icon: 'https://mightbehuman-creator.github.io/pagesavepublicsite/android-chrome-192x192.png',

                });

                // messaging.onMessage((payload) => {
                //     cl('Message received. ', payload);
                //     // Update the UI to include the received message.
                //     // var n = new Notification('My Great Song');
                //     cl(payload);
                //     // const title = payload.notification.title;
                //     // const body = payload.notification.body;
                //     // const notification = new Notification(title, {
                //     //     body: body,
                //     //     icon: 'https://mightbehuman-creator.github.io/pagesavepublicsite/android-chrome-192x192.png',
                //     //     vibrate: true
                //     // });
                //     const n  = new Notification('The next notification will be 1 day later',{
                //         body: body,
                //         icon: 'https://mightbehuman-creator.github.io/pagesavepublicsite/android-chrome-192x192.png',

                //     });
                // });

                // reading the current state
                // const { archives: __, pending: _a } = state;
                // const { [id]: _, ...rest } = state.pending;
                const newPending = { ...state.pending };
                delete newPending[id];
                const newArchives = state.archives.map((item, idx) => {
                    if (item.id === id) {
                        cl('found match at index:' + idx);
                        return {
                            ...item,
                            alerton: true,
                            nextalertdate: newAlertString,
                        }
                    }
                    return item;
                });
                return {
                    ...state,
                    // pending: {
                    //     ...rest,
                    // },
                    pending: newPending,
                    archives: newArchives,
                };
            })
            .addCase(turnOffAlertAsync.pending, (state, action) => {
                cl('turning off alertpending');
                // cl(action.payload);
                // return {
                //     ...state,
                //     [state.pending]: {
                //         ...state.pending,
                //         [action.payload]: true,
                //     },
                // }
            })
            .addCase(turnOffAlertAsync.fulfilled, (state, action) => {
                cl('turning off alert complete');
                cl(action);
                cl(`payload: ${action.payload}`);

                let id = action.payload;
                // const { [id]: _x, ...rest } = state.pending;
                cl(`id to remove from pending in turning off: ${id}`);
                const newPending = { ...state.pending };
                delete newPending[id];

                const newArchives = state.archives.map((item, idx) => {
                    if (item.id === id) {
                        cl('match at idx:' + idx);
                        return {
                            ...item,
                            alerton: false,
                        }
                    }
                    return item;
                });
                return {
                    ...state,
                    // pending: {
                    //     ...rest,
                    // },
                    pending: newPending,
                    archives: newArchives,
                };
            })
            .addCase(registerMessageTokenAsync.pending, (state, action) => {
                //TODO: i should add some management if registering token failed so dont allow alerts
            })
            .addCase(registerMessageTokenAsync.fulfilled, (state, action) => {
                //TODO: i should add some management if registering token failed so dont allow alerts
            })
    }
});


export const { setPending } = archiveSlice.actions;
export const selectArchives = (state) => state.archive.archives;
export const selectStatus = (state) => state.archive.status;
export const selectPendingAlertId = (state) => state.archive.pending;
export const selectAlertOnStatusById = (id) => (state) => {
    let index;
    for (let i in state.archive.archives) {
        if (state.archive.archives[i].id === id) {
            index = i
            cl('match at index:' + index);
            break;
        }
    }
    return state.archive.archives[index].selectAlertOnStatusById;
}
export default archiveSlice.reducer;