function SingleArchive(props) {
    cl('props withing single archive');
    cl(props);
    const classes = useStyles();
    const dispatch = useDispatch();
    // const [isAlertOn, setAlert] = React.useState(props.alerton);
    // const isAlertOn = useSelector(selectAlertOnStatusById(props.id));
    const isAlertOn = props.alerton;

    // i use selector in each archive, may not be efficient
    const pendingObject = useSelector(selectPendingAlertId);
    const isAlertPending = pendingObject.hasOwnProperty(props.id);
    // const humanizedNextAlertDate = 

    // check whether it is pending
    let labelText = isAlertOn ? (
        isAlertPending ? "Turning off alert..."
            // : "Next alert at " + humanizedNextAlertDate
            : "Next alert at " + getFriendlyTime(props.nextalertdate)
    )
        : (isAlertPending ? "Turning on alert..."
            : "Turn on alert");

    const handleChange = async () => {
        // check notificaiton permission
        let registration = window.registration;
        // Request permission and get token.....
        Notification.requestPermission().then(function () {
            cl('Have permission ');
            return messaging.getToken({
                serviceWorkerRegistration: registration,
                vapidKey: "BJy2tod0IHuw43QulYTMh8fkz6MXsrst_avO9feOrpZVdnStqiK4oVYWZ0cCueVf_OqWUcmuIxi-a8TZN6O21hU"
            });
        }).then((currentToken) => {
            if (currentToken) {
                // setMsgToken(currentToken);
                cl("token");
                cl(`currentToken: ${currentToken}`);
                // const cookies = new Cookies();
                // cookies.set('msgtoken', currentToken, { path: '/', maxAge: 2 * 365 * 24 * 60 * 60 });
                // cl(cookies.get('token'));
                // localStorage.setItem('msgtoken', currentToken);
                // setOpen(true);
                // setPermissionLoading(false);

                // maybe have to store it locally or something, if during different session it differs, check this later
                dispatch(registerMessageTokenAsync(currentToken));
                // sub to 
                if (!isAlertOn) {
                    // turning off submit a request
                    dispatch(turnOnAlertAsync(props.id));
                } else {
                    //turn on

                    dispatch(turnOffAlertAsync(props.id));
                }


            } else {
                cl('No registration token available. Request permission to generate one.');
                dispatch(showCustomMsg('Have you enabled notification?'));
                // setSnackbarMsg('Please enable push notification and try again');
                // setSnackbarOpen(true);
                // setPermissionLoading(false);
            }
        }).catch((err) => {
            cl('An error occurred while retrieving token. ', err);
            // send a notificaiton
            dispatch(showCustomMsg('You need to  enable notification for the alerts to work'));

            // setSnackbarMsg('Please enable push notification and try again');
            // setSnackbarOpen(true);
            // setPermissionLoading(false);
        });

        // setAlert(!isAlertOn);
    };

    return (
        <div className={props.idx === 0 ? classes.firstArchive : classes.archivecontainer} >


            <Typography variant="h5" gutterBottom>
                <Link href={props.url} target="_blank" rel="noopener noreferrer nofollow" >
                    <span>{props.title}</span>
                </Link>
            </Typography>
            <div className={classes.gridcontainer}>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img0B} target="_blank" rel="noopener noreferrer nofollow" >
                            <div className={classes.hovereffect}>
                                <img alt="" className={classes.imgofpage} src={props.img0} />
                                <div className={classes.hovertext}>
                                    <Typography variant='h4'
                                        align='center'
                                        style={{
                                            // color: '#4B6A5E',
                                            color: 'white',
                                        }}
                                    >
                                        Phone view
                                    </Typography>
                                </div>
                            </div>
                        </a>
                    </Paper>
                </Container>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img1B} target="_blank" rel="noopener noreferrer nofollow">
                            <div className={classes.hovereffect}>
                                <img alt="" className={classes.imgofpage} src={props.img1} />
                                <div className={classes.hovertext}>
                                    <Typography
                                        variant='h4'
                                        align='center'
                                        style={{
                                            color: 'white',
                                        }}
                                    >
                                        Tablet view
                                    </Typography>
                                </div>
                            </div>
                        </a>
                    </Paper>
                </Container>
                <Container className={classes.fillheight}>
                    <Paper elevation={3} className={classes.paper}>
                        <a href={props.img2B} target="_blank" rel="noopener noreferrer nofollow">
                            <div className={classes.hovereffect}>
                                <img alt="" className={classes.imgofpage} src={props.img2} />
                                <div className={classes.hovertext}>
                                    <Typography
                                        variant='h4'
                                        align='center'
                                        style={{
                                            color: 'white',
                                        }}
                                    >
                                        Desktop view
                                    </Typography>
                                </div>
                            </div>
                        </a>
                    </Paper>
                </Container>
            </div>
            <Container style={{ paddingTop: "16px", display: "flex", justifyContent: "space-between" }}>
                <Link>

                </Link>
                <FormControlLabel
                    control={<Switch color="primary" checked={isAlertOn} disabled={isAlertPending} onChange={handleChange} name="alert" />}
                    // labelPlacement="bottom"
                    labelPlacement="start"
                    label={labelText}
                />
            </Container>
        </div >
    );
}

export default function Archive() {
    const dispatch = useDispatch();
    // select the archives
    let archives = useSelector(selectArchives, shallowEqual);
    let status = useSelector(selectStatus);
    cl('reverse');
    // window.aaa = archives;
    // let reverseArchives = [...archives].reverse(); // this does not update the statesomehow
    // i move the sorting to server side
    let reverseArchives = [...archives]// this does not update the statesomehow
    // let reverseArchives = [];
    let [page, setPage] = useState(1);

    const classes = useStyles();

    useEffect(() => {
        cl('running archive mounting stuff');
        //dispatch

        // dispatch(fetchArchivesAsync()); // why is it mounted even when it is not visible yet
        // this would be dispatched by the queue_change saga
        // Initialize Firebase
        //TODO: error checking, and make this more secure in the future
        let email = Cookies.get('email');
        var queueRef = db.ref('queue/' + replaceFirebaseInvalidCharInKey(email)); // add the email hereTODO:
        queueRef.on('value', (snapshot) => {
            const data = snapshot.val();
            cl("queue changes");
            cl(data);

            // updateStarCount(postElement, data);
            // dispatch(updateUrls(data));
            dispatch({
                type: 'QUEUE_CHANGE',
                payload: data,
            });
        });

    }, [dispatch]);

    function handlePageChange(e, v) {
        cl(v);
        setPage(v);
    }

    // const diskanimation = useSpring({
    //     loop: true,
    //     to: [
    //         { opacity: 1, color: '#ffaaee' },
    //         { opacity: 0, color: 'rgb(14,26,19)' },
    //     ],
    //     from: {
    //         opacity: 0, color: 'red',
    //     },
    //     config: {
    //         duration: 3000,
    //     }

    // })

    return status !== "loading" && (
        <div style={{
        }}>

            <div style={{
                position: "relative",
                marginTop: '72px',
            }}>
                {/* <animated.div
                    style={diskanimation}
                >
                    <img src={diskimg} style={{
                        position: "absolute",
                        right: "80vw",
                        width: "32px",
                        height: "32px",
                        transform: "rotate(45deg)",

                    }} />
                </animated.div> */}
                <Typography variant="h4" className={classes.archiveTitle} gutterBottom>
                    Your Personal Archive
                </Typography>
            </div>
            {reverseArchives.filter(
                (x, idx) => idx >= (page - 1) * EACH_PAGE_COUNT
                    && idx < page * EACH_PAGE_COUNT).map(
                        (a, idx) => (<SingleArchive idx={idx} key={a.id} {...a} />)
                    )}
            {reverseArchives.length === 0 &&
                <div>
                    {/* <Lottie
                        animationData={animationData}
                        style={{
                            height: "200px",
                            width: "200px",
                        }}
                    /> */}
                    <div className={classes.imgwrapper}>
                        <img alt='empty state' src={emptyimg} style={{
                            width: '100%',
                        }} />
                        <Typography
                            style={{
                                marginTop: '32px',
                                maxWidth: '50ch',
                                marginLeft: 'auto',
                                marginRight: 'auto',
                            }}
                            variant="body1"
                        >
                            Wow. It looks empty here. Add a new page to your personal archive using the form above.
                        </Typography>
                    </div>
                </div>}
            <div className={classes.centering}>
                <Pagination
                    page={page}
                    color="primary"
                    onChange={handlePageChange}
                    count={Math.ceil(reverseArchives.length / 5)}
                />
            </div>
        </div>
    );
}