import axios from 'axios';
import { getBaseEndPoint, cl } from '../../helper/util';

export async function fetchArchives() {
    let result;
    try {
        result = await axios.get(getBaseEndPoint() + '/pagesave',
            {
                withCredentials: true,
            });
        cl('fetching archive in axios');
        cl(result.data); // this is an array
        cl(result.status);
        //TODO: refactor this to unpack it here instead of bubbling it thorugh so many steps
        return result;
    } catch (e) {
        cl('fetchin archive some error');

        cl(e.response.status);
        if (e.response.status === 403) {


        }
    }
}

