export async function registerMessageToken(token) {
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/pagesave/registertoken',
            {
                token,
            },
            {
                withCredentials: true,
            });
        cl('Registering token');
        cl(result.data);
        cl(result.status);
        //TODO: refactor this to unpack it here instead of bubbling it thorugh so many steps
        return true;
    } catch (e) {
        cl('Registering token failed');

        cl(e);
        cl(e.response.status);
        if (e.response.status === 403) {


        }
        return false;
    }
}
