import { configureStore } from '@reduxjs/toolkit';
import submissionBarReducer from '../features/submissionbar/submissionbarSlice';
import loginReducer from '../features/login/LoginSlice';
import archiveReducer from '../features/archive/archiveSlice';
import signupReducer from '../features/signup/SignupSlice';
import snackbarReducer from '../features/globalsnackbar/GlobalSnackbarSlice';
import progressdialogReducer from '../features/progressdialog/progressdialogSlice';
import createSagaMiddleware from 'redux-saga';
import rootSaga from '../sagas';
import { logoutAsync } from '../features/login/LoginSlice';
import { combineReducers } from 'redux';
import { cl } from '../helper/util';


const sagaMiddleware = createSagaMiddleware()

const combinedReducer = combineReducers({
  // counter: counterReducer,
  submissionbar: submissionBarReducer,
  archive: archiveReducer,
  progressdialog: progressdialogReducer,
  login: loginReducer,
  signup: signupReducer,
  snackbar: snackbarReducer,
  // navbar: navbarReducer,
});

const rootReducer = (state, action) => {
  cl(logoutAsync.fulfilled + "");
  cl(logoutAsync.fulfilled.toString());
  cl('compare to:' + action.type);
  if (action.type === logoutAsync.fulfilled.type) { // check for action type
    //reset the whole state when logout
    state = undefined;
  }
  return combinedReducer(state, action);
};

export const store = configureStore({
  reducer: rootReducer,
  middleware: (getDefaultMiddleware) =>
    [...getDefaultMiddleware(), sagaMiddleware],


});

sagaMiddleware.run(rootSaga)
