import axios from 'axios';
import { getBaseEndPoint, cl } from '../../helper/util';
export async function submitSignup(email, pw) {
    let result;
    try {
        result = await axios.post(getBaseEndPoint() + '/users/signup',
            {
                email: email,
                pw: pw,
            }, { withCredentials: true });
        cl('normal');
        cl(result.status);
        // get the new user cookie
        // // cl('success cookie');
        // // cl(JSON.stringify(result.headers));
        // // cl('docum');
        // cl(JSON.stringify(document.cookie));
        //cookie already auto set
        return {
            success: true,
            email: email,
        };
    } catch (e) {
        cl('some stuff detected');

        cl(e.response.status);
        if (e.response.status === 409) {
            cl('user already exist');
            return {
                success: false,
                err: 'user already exist',

            };
        } else { // other code means error
            cl('some server error, try again alter');
            return {
                success: false,
                err: 'Try again later',

            };
        }
        // if it is 409 means user exist
        //otherwise try again later
    }


}